import { useState, useEffect, useRef } from "react";
import Link from "next/link";
import { FaChevronLeft, FaChevronRight } from "react-icons/fa";
import Navbar from "../components/Navbar";
import { supabase } from "../lib/supabaseClient";

const GameCarousel = ({
  title,
  games,
}: {
  title: string;
  games: { name: string; image: string; badge?: string }[];
}) => {
  const carouselRef = useRef<HTMLDivElement>(null);

  const scroll = (direction: "left" | "right") => {
    if (carouselRef.current) {
      const scrollAmount = direction === "left" ? -300 : 300;
      carouselRef.current.scrollBy({ left: scrollAmount, behavior: "smooth" });
    }
  };

  return (
    <section className="mb-12">
      <h2 className="text-3xl font-bold mb-4">{title}</h2>
      <div className="relative">
        <button
          onClick={() => scroll("left")}
          className="absolute left-0 top-1/2 -translate-y-1/2 bg-gray-700 p-2 rounded-full text-white z-10 hover:bg-gray-600 transition"
        >
          <FaChevronLeft />
        </button>
        <button
          onClick={() => scroll("right")}
          className="absolute right-0 top-1/2 -translate-y-1/2 bg-gray-700 p-2 rounded-full text-white z-10 hover:bg-gray-600 transition"
        >
          <FaChevronRight />
        </button>

        <div
          ref={carouselRef}
          className="flex space-x-6 overflow-x-auto scrollbar-hide py-2"
        >
          {games.map((game, i) => (
            <div
              key={i}
              className="min-w-[200px] bg-gray-800 rounded-xl shadow-lg hover:scale-105 transition-transform overflow-hidden relative"
            >
              <img src={game.image} alt={game.name} className="w-full h-48 object-cover" />
              <div className="p-3">
                <h3 className="font-semibold">{game.name}</h3>
                {game.badge && (
                  <span className="inline-block mt-2 bg-blue-600 text-xs px-2 py-1 rounded-full">
                    {game.badge}
                  </span>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default function Home() {
  const [hero, setHero] = useState<any[]>([]);
  const [promo, setPromo] = useState<any[]>([]);
  const [novidades, setNovidades] = useState<any[]>([]);
  const [maisJogados, setMaisJogados] = useState<any[]>([]);
  const [emBreve, setEmBreve] = useState<any[]>([]);
  const [selectedHero, setSelectedHero] = useState(0);

  useEffect(() => {
    async function fetchGames() {
      const { data, error } = await supabase.from("games").select("*");
      if (error) return console.error("Erro Supabase:", error);

      // Mapear categorias do banco para seções
      setHero(data.filter((g) => g.category === "RPG").map((g) => ({ name: g.title, image: g.image })));
      setPromo(data.filter((g) => g.category === "Indie").map((g) => ({ name: g.title, image: g.image })));
      setNovidades(data.filter((g) => g.category === "Ação").map((g) => ({ name: g.title, image: g.image })));
      setMaisJogados(data.filter((g) => g.category === "MOBA").map((g) => ({ name: g.title, image: g.image })));
      setEmBreve(data.filter((g) => g.category === "Tiro").map((g) => ({ name: g.title, image: g.image })));
    }

    fetchGames();
  }, []);

  return (
    <div className="bg-gray-900 text-white min-h-screen">
      {/* Navbar com fundo escuro */}
      <div className="fixed top-0 left-0 w-full z-50 bg-gray-900">
        <Navbar />
      </div>

      <main className="pt-20">
        {/* Hero Section */}
        {hero.length > 0 && (
          <section className="relative flex max-w-7xl mx-auto px-6 h-[500px] mt-6">
            <div className="flex flex-col space-y-4 w-20">
              {hero.map((game, i) => (
                <img
                  key={i}
                  src={game.image}
                  alt={game.name}
                  className={`h-24 w-full object-cover rounded-lg cursor-pointer border-2 transition-all ${
                    selectedHero === i ? "border-blue-500" : "border-transparent"
                  }`}
                  onClick={() => setSelectedHero(i)}
                />
              ))}
            </div>

            <div className="flex-1 relative ml-6 rounded-lg overflow-hidden shadow-lg">
              <img
                src={hero[selectedHero].image}
                alt={hero[selectedHero].name}
                className="w-full h-full object-cover brightness-75"
              />
              <div className="absolute bottom-8 left-8">
                <h2 className="text-4xl sm:text-5xl font-bold">{hero[selectedHero].name}</h2>
                <Link
                  href="/games"
                  className="mt-4 inline-block bg-blue-600 hover:bg-blue-700 px-6 py-3 rounded-lg text-lg font-semibold transition-shadow shadow-lg"
                >
                  Jogar Agora
                </Link>
              </div>
            </div>
          </section>
        )}

        {/* Carrosséis */}
        <main className="max-w-7xl mx-auto px-6 py-12 space-y-16">
          <GameCarousel title="Promoções em Destaque" games={promo} />
          <GameCarousel title="Novidades" games={novidades} />
          <GameCarousel title="Mais Jogados" games={maisJogados} />
          <GameCarousel title="Em Breve" games={emBreve} />
        </main>
      </main>
    </div>
  );
}
